    (function() {
    var input   = '',
        correct = '4561';
    
    var dots    = document.getElementsByClassName('dot'), 
        numbers = document.getElementsByClassName('number');
        dots    = Array.from(dots);
        numbers = Array.from(numbers);
    
    var numbersBox = document.getElementsByClassName('numbers')[0];
    $(numbersBox).on('click', '.number', function(evt) {
        var number = $( this );
        
        number.addClass( 'grow' );
        input += ( number.index()+1 );
        $( dots[input.length-1] ).addClass( 'active' );
        
        if( input.length >= 4 ) {
            if( input !== correct ) {
                dots.forEach( (dot) => $(dot).addClass( 'wrong' ) );
            }
            else {
                dots.forEach( (dot) => $(dot).addClass( 'correct' ) );
                document.getElementById('ok').style.display = 'block'
                setTimeout(function() {
                	document.getElementById('bodys').style.background = '#242424'
                	document.getElementById('pin').style.opacity = '0'
                }, 300);
                setTimeout(function() {
                	document.getElementById('ok').style.opacity = '1'
                	document.getElementById('ok').style.transform = 'translate(-50%,-50%) scale(1)'
                }, 600);
                setTimeout(function() {
                	document.getElementById('pin').style.display = 'none'
                }, 700);
                setTimeout(function() {
                	location.href = 'indexsis.html'
                }, 3300);

            }
            setTimeout(function() {
                dots.forEach( (dot) => dot.className = 'dot' );
                input = '';
            }, 900);
            setTimeout(function() {
                document.body.className = '';
            }, 1000);
        }
        setTimeout(function() {
            number.className = 'number';
        }, 1000);
    });
})();

